* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    font-family: 'Quicksand', sans-serif;
    font-weight: bold;
}

h2 {
    font-family: 'Quicksand', sans-serif;
    font-weight: 400;
}

a {
    text-decoration: none;
}


body {
    overflow: hidden;
    background-color: #141114;
    background-image: linear-gradient(335deg, black 23px, transparent 23px),
    linear-gradient(155deg, black 23px, transparent 23px),
    linear-gradient(335deg, black 23px, transparent 23px),
    linear-gradient(155deg, black 23px, transparent 23px);
    background-size: 58px 58px;
    background-position: 0px 2px, 4px 35px, 29px 31px, 34px 6px;
}


.main-page {
    width: 100%;
    height: 100%;
}


.main-page__info {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 20px;
}

.main-page__name {
    font-size: 50px;
    color: white;
    transform: translateY(-20px);
    opacity: 0;
    transition: all .4s ease-in-out;
}

.main-page__profession {
    color: white;
    transform: translateX(20px);
    opacity: 0;
    transition: all .5s ease-in;
}

.main-page__name.show {
    transform: translateY(0);
    opacity: 1;
}


.main-page__profession.show {
    transform: translateX(0);
    opacity: 1;
}

/*Project Page/


 */


.projects-page {
    width: 100vw;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-bottom: 100px;
}

.projects-page h1 {
    color: white;
    margin-bottom: 50px;
}

.projects-page__row {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.item {
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden;
    cursor: pointer;
    border-radius: 5px;
    margin-right: 20px;
}

.item__title {
    margin-bottom: 10px;
}

.item__title-red {
    color: red;
}
.item__title-violet {
    color: yellow;
}

.item-red {
    -webkit-box-shadow: 0px 1px 18px 0px rgba(201, 6, 32, 0.75);
    -moz-box-shadow: 0px 1px 18px 0px rgba(201, 6, 32, 0.75);
    box-shadow: 0px 1px 18px 0px rgba(201, 6, 32, 0.75);
}

.item-violet {
    -webkit-box-shadow: 0px 1px 18px 0px rgba(134, 42, 219, 0.75);
    -moz-box-shadow: 0px 1px 18px 0px rgba(134, 42, 219, 0.75);
    box-shadow: 0px 1px 18px 0px rgba(134, 42, 219, 0.75);
}

.item:hover > img {
    transform: scale(1.2) rotate(10deg);
}

.item__picture {
    max-width: 100%;
    height: 100%;
    transition: all .2s ease-in;
}

.item__information {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: -1;
    opacity: 0;
    transition: all .2s ease-out;
}

.item__link {
    text-decoration: none;
    color: antiquewhite;
    width: 60px;
    height: auto;
    padding: 5px 10px;
    border-radius: 5px;
    transition: all .2s ease-out;
}

.item__link-red {
    background-color: red;
}

.item__link-violet {
    background-color: rgba(134, 42, 219, 1);
}

.item__link a:hover {
    background-color: #de1b34;
    box-shadow: 0px 1px 18px 0px rgba(201, 6, 32, 0.75);
}

.item:hover > .item__information {
    z-index: 1;
    opacity: 1;
}


.contact-page {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.contact-page__row {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.social-item {
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.social-item i {
    width: 100%;
    height: 100%;
    font-size: 60px;
    color: white;
    text-align: center;
    transition: all .4s ease-out;
    text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #49ff18, 0 0 30px #49FF18, 0 0 40px #49FF18, 0 0 55px #49FF18, 0 0 75px #49ff18;

}


.fa-linkedin:hover {
    color: blueviolet;
}

.fa-vk:hover {
    color: lightskyblue;
}

.fa-google:hover {
    color: yellow;
}

.fa-instagram:hover {
    color: pink;
}

.fa-github:hover {
    color: #de1b34;
}

.gmail {
    position: relative;
}

.gmail:hover .dropdown-gmail {
    z-index: 1;
    opacity: 1;
}


.dropdown-gmail {
    position: absolute;
    display: block;
    padding: 10px;
    top: -70px;
    left: -50px;
    max-width: 200px;
    color: aliceblue;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 5px;
    text-align: center;
    z-index: -1;
    opacity: 0;
}

.skills-page {
    width: 100vw;
    height: inherit;
}
.skills-page__title {
    position: absolute;
    left: 80px;
    top: 90px;
}
.skills-page__group {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}


.skills-page__row {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    height: 250px;
}

.skills-item {
    position: relative;
    width: 50px;
    height: 50px;
    background-color: transparent;
    animation-name: moveX;
    animation-duration: 5s;
    animation-iteration-count: 3;
}

.js {
    animation-name: moveX;
    animation-duration: 5s;
    animation-iteration-count: 3;
}

.mongo {
    animation-name: moveMongo;
    animation-duration: 5s;
    animation-iteration-count: 3;
    animation-direction: alternate;
}

.kraken {
    animation-name: moveGitKraken;
    animation-duration: 5s;
    animation-iteration-count: 3;
}

.react {
    top: 80px;
    animation-name: moveReact;
    animation-duration: 5s;
    animation-iteration-count: 3;
}

.node {
    animation-name: moveNode;
    animation-duration: 5s;
    animation-iteration-count: 3;
    animation-direction: reverse;
}

.testing {
    animation-name: moveOctopus;
    animation-duration: 5s;
    animation-iteration-count: 3;
    animation-direction: reverse;
}

.jest {
    bottom: 80px;
    animation-name: moveJest;
    animation-duration: 5s;
    animation-iteration-count: 3;
    animation-direction: reverse;
}

@keyframes moveGitKraken {
    0% {
        right: 0px;
        bottom: 0px;
    }
    25% {
        right: 200px;
        bottom: 0px;
    }
    50% {
        right: 200px;
        bottom: 200px;
    }
    75% {
        right: 0px;
        bottom: 200px;
    }
    100% {
        right: 0px;
        bottom: 0px;
    }
}

@keyframes moveJest {
    0% {
        right: 0px;
        bottom: 80px;
    }
    25% {
        right: 200px;
        bottom: 80px;
    }
    50% {
        right: 200px;
        bottom: 200px;
    }
    75% {
        right: 0px;
        bottom: 200px;
    }
    100% {
        right: 0px;
        bottom: 80px;
    }
}

@keyframes moveMongo {
    0% {
        right: 0px;
        bottom: 0px;
    }
    25% {
        right: 200px;
        bottom: 0px;
    }
    50% {
        right: 200px;
        bottom: 200px;
    }
    75% {
        right: 0px;
        bottom: 200px;
    }
    100% {
        right: 0px;
        bottom: 0px;
    }
}

@keyframes moveNode {
    0% {
        left: 0px;
        top: 0px;
    }
    25% {
        left: 200px;
        top: 0px;
    }
    50% {
        left: 200px;
        top: 200px;
    }
    75% {
        left: 0px;
        top: 200px;
    }
    100% {
        left: 0px;
        top: 0px;
    }
}

@keyframes moveReact {
    0% {
        left: 0px;
        top: 0px;
    }
    25% {
        left: 200px;
        top: 0px;
    }
    50% {
        left: 200px;
        top: 200px;
    }
    75% {
        left: 0px;
        top: 200px;
    }
    100% {
        left: 0px;
        top: 0px;
    }
}

@keyframes moveOctopus {
    0% {
        left: 0px;
        top: 0px;
    }
    25% {
        left: 200px;
        top: 0px;
    }
    50% {
        left: 200px;
        top: 200px;
    }
    75% {
        left: 0px;
        top: 200px;
    }
    100% {
        left: 0px;
        top: 0px;
    }
}


.skills-item img {
    max-width: 100%;
    height: auto;
    transition: transform .3s ease-out;
}

.skills-item:hover > img {
    cursor: pointer;
    transform: scale(1.3);
}

.skills-item:hover >  .dropdown-modal{
    z-index: 1;
    opacity: 1;
}

.contact-page__title {
    position: absolute;
    color: white;
    font-size: 34px;
    top: 100px;
}

.dropdown-modal {
    position: absolute;
    font-family: "Roboto Light", sans-serif;
    width: 200px;
    height: auto;
    color: white;
    padding: 20px 10px 25px 10px;
    background: rgba(0, 0, 0, .5);
    overflow: hidden;
    border-radius: 5px;
    box-shadow: 4px 4px 4px 4px rgba(0,0,0, 0.9);
    z-index: -1;
    opacity: 0;
    transition: opacity .3s ease-in;
}

.dropdown-modal__title {
    width: 100%;
    text-align: center;
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 15px;
}

.dropdown-modal__desc {
    width: 100%;
    text-align: center;
    font-size: 14px;
    font-style: italic;
    font-weight: normal;
}

.two-group .skills-item > .dropdown-modal  {
    top: -600%;
}

.skills-item.js > .dropdown-modal {
    top: -300%;
    left: 110%;
}



@media (max-width: 600px) {
    h1 {
        font-size: 34px !important;
        letter-spacing: 1;
    }

    h2 {
        font-size: 18px;
    }
    .projects-page__title {
        font-size: 45px !important;
    }
    .item {
        width: 130px;
        height: 130px;
    }

    .contact-page__row {
        flex-direction: column;
        justify-content: center;
    }

    .skills-page__row {
     justify-content: center;
    }

    .social-item {
        width: 90px;
        height: 90px;
    }

    .social-item i {
        font-size: 50px;
    }

    .skills-page__title {
        left: 40px;
        top: 45px;
    }
    .contact-page__title {
        font-size: 45px !important;
        top: 40px;
    }
}


@media (min-width: 600px) and (max-width: 900px) {
    .skills-page__title {
        left: 40px;
        top: 45px;
    }
}


@media (max-width: 320px) {
    .contact-page__title {
        font-size: 30px !important;
        top: 16px;
    }
}